body {
    margin: 0;
    padding: 0;
    overflow-y: scroll;  
    background: #f3f3f3;
    word-break: break-word;
}

.oauth {
    position: absolute;
    display: -ms-flexbox;
    display: flex;
    width: 100%;
    height: 100%;
    -ms-flex-pack: center;
        justify-content: center;
    -ms-flex-align: center;
        align-items: center;
}

.oauth .box{
    padding: 50px 0;
    max-height: 700px;
    max-width: 400px;
    width: 80%;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-sizing: border-box;
            box-sizing: border-box;
    border-radius: 2px;
    -webkit-box-shadow: 0 1px 3px rgba(26,26,26,.1);
            box-shadow: 0 1px 3px rgba(26,26,26,.1);
    background: #fff;
    -ms-flex-align: center;
        align-items: center;
    -ms-flex-direction: column;
        flex-direction: column;
}

.oauth .box .button{
    background: #0f88eb;
    color: #ffffff;
    padding: 10px;
    width: 80%;
    min-width: 0;
    min-height: 0;
}




